// reset
html, body, div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, address, cite, code, del, dfn, em, font, img, ins, q, s, samp, strike, strong, sub, sup, dl, dt, dd, ol, ul, li, fieldset, form, label, legend {
    line-height: 1.3em;
    border: 0 none;
}

body {
    margin: 0 auto;
    font-size: 100%;
    line-height: 1.3em;
    font-family: $font-base-stack;
    word-break: normal;
    text-size-adjust: 100%;
}

sub,sup {
    font-family:inherit;
}

// for screen reader
.skip_nav {
    position: absolute;
    z-index: 9999;
    width: 100%;

    a {
        display: block;
        overflow: hidden;
        width: inherit;
        height: 0;
        margin: 0 auto;
        height: 1px;
        margin-bottom: -1px;
        white-space: nowrap;
        text-align: center;
        text-indent: -9999px;
    }

    a:focus,
    a:active {
        height: 34px;
        padding: 1px;
        border-bottom: 2px solid #ebe8e7;
        background: #666;
        color: #333;
        font-weight: bold;
        font-size: 14px;
        text-indent: 0;
        line-height: 33px;
    }
}


// heading group
h1,h2,h3,h4,h5,h6 {
    font-family:inherit;
    line-height:1.1;
    font-weight:normal;
	margin:0;
}

h2 {
	font-weight:$light;
	color:$white
}

h3 {
	font-weight:$light;
	color:$white
}



// zero club
h1,h2,h3,h4,h5,h6,
figure, figcaption,
dt,dd,dl,
iframe,
fieldset
{
    padding:0;
    margin:0;
}

iframe {
    border:0;
}

// typo
address, em {
    font-style:normal;
}

em {
    font-weight:500;
}


// Inheritance font size
input,textarea,table,th,td,fieldset,button {
	background-color:inherit;
    font-size:inherit;
}


// base ux feedback (GUI - mouse)
input[type="submit"],
button {
    cursor:pointer;
}


// reset to related table
table {
    border-collapse: collapse;
	border-spacing: 0;
    width:100%;

    th:first-letter,
    td:first-letter {
        text-transform:uppercase;
    }
}




// --------------------------------------------------------------------------------
// --------------------------------------------------------------------------------
// --------------------------------------------------------------------------------





// link
a {
	color:inherit;
    cursor:pointer;
}

a:link {
    text-decoration:none;
}



// --------------------------------------------------------------------------------
// --------------------------------------------------------------------------------
// --------------------------------------------------------------------------------


// input
input[type="text"] {
	color:inherit;
	border-color:transparent;
}


input[type="email"]::placeholder {
	color:$gray;
}

input[type='checkbox'] {
	@include screen_reader();

	+ label {
		padding-left:1.5rem;
		position:relative;
		&:before,
		&:after {
			content:'';
			left:0;
			top:calc((20px - 1em) / 2);
			position:absolute;
			display:inline-block;
			font-size:inherit;
			width:1rem;
			height:1rem;
			background:#600;
			line-height:inherit;
		}
	}
}

// button
button {
	color:inherit;
	border-color:transparent;

}


